import { format } from "date-fns";

function hoursArray () {

  const result = new Array();

  for (var i = 0; i < 24; i++) {
    if (i == 0) {
      result[i] = "";
    } else if (i < 12) {
      result[i] = ` ${i}`;
    } else if (i == 12) {
      result[i] = ` ${i}`
    } else {
      result[i] = ` ${i-12}`
    }
  }

  return result;
}

export default hoursArray;